@font-face {
    font-family: brandonGrotesque;
    src: url(../../Fonts/BrandonGrotesque-Bold.woff);
};
p, textarea {
    font-family: "open sans";
};
h1, h2, h3, h4, h5, h6, a, label, button, input{
    font-family: brandonGrotesque;
    text-transform: uppercase;
    
};

a {
    text-decoration: none;
    color: $color1;
    &:visited{
        color: $color4;
    }
    &:hover {
        color: $color2;
    }

    

};


